'use strict';
/**
 * @api JUMP_IN(url) JUMP_IN
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiParam {string} url 目标URL地址
 * @apiDescription APP 在当前窗口打开页面
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * JUMP_IN('https://www.baidu.com')  外部地址
 * JUMP_IN('https://gift.yingyinglicai.com/activity/invite/???')  活动地址
 * @apiParamExample {string} 返回值
 * 无
 **/

/**
 * todo JUMP
 */
import {WinJSDec, strError} from './APP/config';
import BROWSER from './BROWSER';

var name = 'JUMP_IN';
var JUMP_IN = url => {
  if (typeof url !== 'string') {
    throw new TypeError(`[${name}] ${strError}`);
  }
  if (BROWSER.isClient) {
    WinJSDec(_ => {
      window.WinJSBridge.call('webview', 'openInCurrentWindow', {url});
    });
  } else {
    window.location.href = url;
  }
};
export default JUMP_IN;
